<template>
  <div class="my-container">
    
    <img class="back_arc" src="@/assets/images/my_top_back.png" alt="">
    
    <div v-if="userInfo" class="top-area f-row-between">
      <img
        class="avatar"
        :src="userInfo.avatar"
        alt=""
      >
      <div class="detail f-row-between">
        <div class="content f-col-between">
          <p class="nickname one-ellipsis">
            {{ userInfo.nickName }}
          </p>
          <div class="status f-i-c">
            <img
              :src="userInfo.isMember ? vipYesIcon : vipNoIcon"
              alt=""
            >
            <span>
              {{ userInfo.isMember ? `${userInfo.expTime}到期` : '未开通' }}
            </span>
          </div>
        </div>
      </div>
    </div>

    <div class="tab-box f-row-between">
      <div @click="toRoutePage( '/my/my-listen-history' )" class="tab f-col-between-c">
        <img src="@/assets/images/my_tab_history.png" alt="">
        <p>
          收听历史
        </p>
      </div>
      <div @click="toRoutePage( '/my/my-bought' )" class="tab f-col-between-c">
        <img src="@/assets/images/my_tab_order.png" alt="">
        <p>
          我的购买
        </p>
      </div>
      <div @click="$router.push( '/my/my-publish' )" class="tab f-col-between-c">
        <img src="@/assets/images/my_tab_comment.png" alt="">
        <p>
          我的发布
        </p>
      </div>
    </div>

    <img class="gift" src="@/assets/images/verify_code_buy_gift.png" alt="">
    
    <div class="menu-box">
      <!-- <div @click="$router.push( '/my/my-baby/list' )" class="menu f-row-between-c">
        <div class="content f-row-between-c">
          <p class="title">
            宝宝信息
          </p>
          <p class="intro">
            
          </p>
        </div>
        <img src="@/assets/images/my_more.png" alt="">
      </div> -->
      <!-- <div @click="$router.push( '/my/mechanism-info' )" class="menu f-row-between-c">
        <div class="content f-row-between-c">
          <p class="title">
            机构信息
          </p>
          <p class="intro intro-red">
            激活机构服务
          </p>
        </div>
        <img src="@/assets/images/my_more.png" alt="">
      </div> -->
      <div class="menu f-row-between-c">
        <div class="content f-row-between-c">
          <p class="title">
            手机号绑定
          </p>
          <p
            @click="toBindPhonePage"
            :class="['intro', encryptPhone ? '' : 'intro-red']"
          >
            {{ encryptPhone || '去绑定' }}
          </p>
        </div>
        <img src="@/assets/images/my_more.png" alt="">
      </div>
      <!-- <div class="menu f-row-between-c">
        <div class="content f-row-between-c">
          <p class="title">
            青豆书坊
          </p>
          <p class="intro">
            去买书
          </p>
        </div>
        <img src="@/assets/images/my_more.png" alt="">
      </div> -->
      <div class="menu f-row-between-c">
        <div class="content f-row-between-c">
          <p class="title">
            联系客服
          </p>
          <p class="intro">
            
          </p>
        </div>
        <img src="@/assets/images/my_more.png" alt="">
      </div>
    </div>

    <Tabbar class="tabbar-fixed-bottom" />
    
    <div class="bottom-block-135" />
  </div>
</template>

<script>
import Tabbar from '@/components/tabbar/Tabbar.vue';
import { db } from '@/utils/lowdb';

export default {
  components: {
    Tabbar
  },

  data() {
    return {
      userInfo: null,
      encryptPhone: '',
      
      vipYesIcon: require('../../assets/images/my_vip_yes.png'),
      vipNoIcon: require('../../assets/images/my_vip_no.png')
    }
  },

  created() {
    document.title = '我的';
  },

  mounted() {
    this.getLocalUserInfo();
  },

  methods: {
    // todo 跳转至绑定手机号的页面
    toBindPhonePage() {
      if (this.userInfo.mobileNo) return ;
      this.$router.push({
        name: 'bind-phone'
      })
    },

    // todo 获取本地存储中的用户信息
    getLocalUserInfo() {
      this.userInfo = db.get('userInfo').value();
      this.encryptPhone = this.encryptUserPhone( this.userInfo.mobileNo );
    },

    // todo 加密手机号进行展示，只留首位四位
    encryptUserPhone(phone) {
      if (!phone) return null;

      let pho = '', len = phone.length;
      for (let i = 0; i < len; i++) {
        if (i < 4 || (i + 4 > len - 1)) {
          pho += phone.charAt(i);
        } else {
          pho += '*';
        }
      }

      return pho;
    },

    toRoutePage(path) {
      this.$router.push( path );
    }
  }
}
</script>

<style lang="scss" scoped>
.my-container {
  width: 100%;
  background: #fff;

  .back_arc {
    position: absolute;
    width: 100%;
  }
  .top-area{
    width: 100%;
    height: 127px;
    padding: 25px 16px 25px 25px;
    box-sizing: border-box;
    position: relative;

    .avatar {
      width: 77px;
      height: 77px;
      border-radius: 50%;
    }
    .detail {
      width: calc(100% - 92px);

      .content {
        padding: 12px 0;
        box-sizing: border-box;
        width: calc(100% - 17px);

        .nickname {
          width: 80%;
          font-size: 18px;
          font-weight: bold;
          color: rgba(255,255,255,1);
        }
        .status {
          
          img {
            width: 20px;
            height: 12px;
          }
          span {
            width: 80%;
            font-size: 13px;
            color: rgba(255,255,255,1);
            margin-left: 5px;
          }
        }
      }
    }
  }

  .tab-box {
    width: calc(100% - 32px);
    padding: 15px 40px;
    background: rgba(255,255,255,1);
    box-shadow: 0px 2px 7px 1px rgba(0, 0, 0, 0.12);
    border-radius: 10px;
    margin: 0 16px;
    position: relative;
    box-sizing: border-box;

    .tab {
      width: 50px;
      height: 55px;

      img {
        width: 30px;
        height: 30px;
      }
      p {
        font-size: 12px;
        color: rgba(153,153,153,1);
      }
    }
  }

  .gift {
    width: calc(100% - 32px);
    margin: 25px 16px 0;
  }

  .menu-box {
    width: 100%;
    padding: 0 16px;
    box-sizing: border-box;

    .menu {
      height: 50px;
      border-bottom: #EEEEEE solid 1px;
      
      .content {
        width: calc(100% - 14px);

        .title {
          font-size: 15px;
          font-weight: 600;
          color: rgba(51,51,51,1);
        }
        .intro {
          font-size: 13px;
          color:rgba(153,153,153,1);
        }
        .intro-red {
          color: rgba(239,60,60,1);
        }
      }
      img {
        width: 7px;
        height: 10px;
      }
    }
  }
}
</style>


